.offers {
  padding: 18px 20px 50px 20px;

  @media (min-width: $tablet) {
    padding: 30px 155px 58px 155px;
  }

  @media (min-width: $desktop) {
    padding: 33px 370px 65px 370px;
  }

  h2 {
    @include font-sans-bold (21px, 700, 26px);

    @media (min-width: $tablet) {
      @include font-sans-bold (30px, 700, 26px);

    }

    @media (min-width: $tablet) {
      padding-bottom: 8px;
    }
  }

  p {
    @include font-sans (14px, 400, 24px);
    padding: 15px;

    @media (min-width: $tablet) {

      padding: 0 0 25px 0;

    }

    @media (min-width: $desktop) {
      width: 100%;
      padding-bottom: 29px;

    }

  }

  span {
    @media (min-width: $tablet) {
      display: block;
    }

  }

  .offers__btn {
    margin: 0 auto;


    @media (min-width: $tablet) {
      width: 458px;
      height: 58px;
    }

  }
}
